body 
  font-family: sans-serif
  margin: 50px
  text-align: center
  background: #1a4705
  color: #f6f7f5

button 
  background: red
  color: blue
  font-size: 20px

.pizza-container
  display: flex
  justify-content: center
  margin-top: 70px

.pizza
  position: relative
  width: 400px
  height: 400px
  background-color: #ffeb3b
  border-radius: 50%
  border: 4px solid orange
  box-shadow: 0 0 0px 10px #dd8126

  .olives
    position: absolute
    width: 400px
    height: 400px
    border-radius: 50%
    z-index: 11
    background-image: radial-gradient(circle at 70px 70px, #455a64 3px, darken(#455a64, 20%) 9px, transparent 10px)
    background-size: 100px 100px

  .pepperonis
    position: absolute
    width: 400px
    height: 400px
    border-radius: 50%
    z-index: 10
    background-image: radial-gradient(circle at 40px 40px, #ea5353 14px, darken(#ea5353, 20%) 16px, transparent 18px)
    background-size: 80px 80px

  .slices
    display: flex
    position: relative
    width: 100%
    height: 100%
    justify-content: center

    .slice
      width: 1px
      height: 100%
      background-color: #e8d83a

    .slice-1
      transform: rotate(30deg)

    .slice-2
      transform: rotate(60deg)

    .slice-3
      transform: rotate(90deg)

    .slice-4
      transform: rotate(120deg)

    .slice-5
      transform: rotate(150deg)

    .slice-6
      transform: rotate(180deg)